import React, {useEffect} from 'react';
import {useDispatch} from "react-redux";
import {getFindByParentIdAsync, useCmnSelector} from "@/store/slices/cmn";
import {Table} from "antd";

const columns = [
    { title: '名称', dataIndex: 'name'},
    { title: '编码', dataIndex: 'dictCode'},
    { title: '值', dataIndex: 'id'},
    { title: '创建时间', dataIndex: 'createTime'}
];

const Dict = () => {
    const dispatch:any = useDispatch();
    const {dictList,loading} = useCmnSelector();
    useEffect(()=>{
        dispatch(getFindByParentIdAsync(1));
    },[])
    return (
        <Table
            pagination={false}
            loading={loading}
            columns={columns}
            expandable={{
                onExpand(expanded, record){
                    if(expanded && record.children.length===0){
                        dispatch(getFindByParentIdAsync(record.id));
                    }
                }
            }}
            dataSource={dictList}
            rowKey={"id"}
        />
    );
};

export default Dict;